/* global document */
import preference from './constants';

/**
 * Defines the default heading type
 * as defined in the widget model.xml
 * @type {string}
 */
const DEFAULT_HEADING_TYPE = 'h1';

/**
 * @name HeadingsController
 * @type {object}
 *
 * @description
 * Headings component
 *
 * @usage
 * <div ng-controller="HeadingsController as $ctrl">
 *   <!-- ... -->
 * </div>
 */
export default function HeadingsController(widget, bbIntent, bus) {
  /**
   * @description
   * List of valid heading values
   *
   * @type {Array}
   * @inner
   */
  const validHeadings = [];
  for (let i = 1; i <= 6; i++) {
    validHeadings.push(`h${i}`);
  }

  const headingType = validHeadings.includes(widget.getStringPreference(preference.HEADING_TYPE)) ?
    widget.getStringPreference(preference.HEADING_TYPE) :
    DEFAULT_HEADING_TYPE;

  const isTitleAsHeading =
    widget.getBooleanPreference(preference.PAGE_TITLE_AS_HEADING) || false;

  const heading = widget.getStringPreference(preference.HEADING) ||
    (isTitleAsHeading ? document.title : '');

  const actionIntentKey = widget.getStringPreference(preference.ACTION_INTENT_KEY);
  const buttonClasses = widget.getStringPreference(preference.BUTTON_CLASSES);
  const buttonTitle = widget.getStringPreference(preference.BUTTON_TITLE);

  /**
   * @name HeadingsController#pageConfig
   * @type {Object}
   * @description Headings configuration
   * @property {string} headingType Type of heading (H1 - H6)
   * @property {string} heading Heading text
   * @property {boolean} isTitleAsHeading Should heading match the active
   * menu item
   */
  const pageConfig = {
    headingType,
    heading,
    isTitleAsHeading,
    buttonClasses,
    buttonTitle,
  };

  let action = null;

  const $onInit = () => {
    action = bbIntent.create(actionIntentKey);

    /**
     * This event (cxp.item.loaded) is deprecated in Mobile SDK version > 3.0
     * and will be removed with the update to widget collection 3 (WC3)
     */
    bus.publish('cxp.item.loaded', {
      id: widget.getId(),
    });

    bus.publish('bb.item.loaded', {
      id: widget.getId(),
    });
  };

  const doAction = () => action();

  return {
    pageConfig,
    doAction,
    /* Lifecycle hooks */
    $onInit,
  };
}




