/**
 * @module widget-bb-headings-ng
 *
 * @description
 * Headings widget
 */
import angular from 'vendor-bb-angular';
import widgetModuleKey, { widgetKey } from 'lib-bb-widget-ng';
import eventBusModuleKey, { eventBusKey } from 'lib-bb-event-bus-ng';
import bbIntentModuleKey, { bbIntentKey } from 'lib-bb-intent-ng';

import Controller from './controller';

export default angular
  .module('widget-bb-headings-ng', [
    eventBusModuleKey,
    bbIntentModuleKey,
    widgetModuleKey,
  ])
  .controller('HeadingsController', [
    widgetKey,
    bbIntentKey,
    eventBusKey,
    /* into */
    Controller,
  ])
  .run([
    bbIntentKey, eventBusKey, widgetKey,
    (bbIntent) => {
      bbIntent.init();
    },
  ])
  .name;



